
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50;margin-top:60px
}
#svg-icon-logo,#animation{animation:changeColor 5s infinite linear
}
@keyframes changeColor{
0%{color:red
}
20%{color:yellow
}
40%{color:blue
}
60%{color:green
}
80%{color:purple
}
100%{color:red
}
}
#container{text-align:left;padding-left:5%
}
#container section{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin:20px 0
}
#container section h4{width:100%
}
#container section .attr{color:#e96900
}
#container section .val{color:#42b983
}
#container section>span{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;height:48px;width:48px;border:1px solid #888;border-radius:4px
}
#container section>span svg{color:#05CE7C
}
#container section code{color:#2973b7;width:80%;border-radius:3px;margin-left:15px;padding:4px 10px;background:#eee;line-height:20px;box-shadow:0 1px 2px rgba(0,0,0,0.125)
}
#container section code strong{color:#111
}
#container section #hover{color:grey
}
#container section #hover:hover{color:gold
}
#container section #click{color:#424242
}
#container section #click:active{color:white
}



